import { applicationContainer } from "../inversify.config";
import { APPLICATION_TYPES } from "../types";
import { DEFAULT_VENDOR_ARR, } from "yugioh-deck-tool-core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
const priceService = applicationContainer.get(APPLICATION_TYPES.PriceService);
const cardService = applicationContainer.get(APPLICATION_TYPES.CardService);
export default defineComponent({
    props: {
        cards: {
            required: true,
            type: Array,
        },
    },
    setup(props, context) {
        const activeCurrency = computed(() => context.root.$store.state.currency.active);
        const priceByVendor = computed(() => new Map(DEFAULT_VENDOR_ARR.map((vendor) => {
            const lookupResult = priceService.getPrice(props.cards, vendor, activeCurrency.value);
            return [vendor, lookupResult];
        })));
        const listMissingCards = (lookupResult) => {
            const cardList = cardService
                .createCountedCardList(lookupResult.missing)
                .join("\n");
            return `Missing prices for ${lookupResult.missing.length} card(s):\n\n${cardList}`;
        };
        const formatPrice = (lookupResult) => priceService.formatPrice(lookupResult.price, activeCurrency.value);
        return { priceByVendor, formatPrice, listMissingCards };
    },
});
