import { applicationContainer } from "@/inversify.config";
import { APPLICATION_TYPES } from "@/types";
const deckService = applicationContainer.get(APPLICATION_TYPES.DeckService);
export const DECK_NAME_UPDATE = "DECK_NAME_UPDATE";
export const DECK_REPLACE = "DECK_REPLACE";
export const DECK_SORT = "DECK_SORT";
export const DECK_SHUFFLE = "DECK_SHUFFLE";
export const DECK_CARD_ADD = "DECK_CARD_ADD";
export const DECK_CARD_REMOVE = "DECK_CARD_REMOVE";
export const DECK_CARD_REORDER = "DECK_CARD_REORDER";
/**
 * Deck state.
 *
 * NOTE: Vuex doesnt pick up on map modification, manual assignment is required.
 */
export const deckModule = {
    state: () => {
        return {
            active: deckService.createEmptyDeck(),
        };
    },
    mutations: {
        [DECK_NAME_UPDATE](state, payload) {
            state.active.name = payload.name;
        },
        [DECK_REPLACE](state, payload) {
            state.active = payload.deck;
        },
        [DECK_SORT](state) {
            state.active.parts = deckService.sort(state.active).parts;
        },
        [DECK_SHUFFLE](state) {
            state.active.parts = deckService.shuffle(state.active).parts;
        },
        [DECK_CARD_ADD](state, payload) {
            state.active.parts = deckService.addCard(state.active, payload.deckPart, payload.card, payload.newIndex).parts;
        },
        [DECK_CARD_REMOVE](state, payload) {
            state.active.parts = deckService.removeCard(state.active, payload.deckPart, payload.card, payload.oldIndex).parts;
        },
        [DECK_CARD_REORDER](state, payload) {
            state.active.parts = deckService.reorderCard(state.active, payload.deckPart, payload.card, payload.oldIndex, payload.newIndex).parts;
        },
    },
};
