import { computed, defineComponent } from "@vue/composition-api";
export default defineComponent({
    props: {
        card: {
            required: true,
            type: Object,
        },
        scaleVertically: {
            required: false,
            type: Boolean,
            default: () => false,
        },
    },
    setup(props, context) {
        const name = computed(() => props.card.name);
        const imgSrc = computed(() => props.card.image.urlSmall);
        return { name, imgSrc };
    },
});
