import { applicationContainer } from "../../inversify.config";
import { APPLICATION_TYPES } from "../../types";
import { SortingOrder, SortingStrategy, } from "yugioh-deck-tool-core/src/main";
import YgoFilter from "../YgoFilter.vue";
import YgoSortingOptions from "./YgoSortingOptions.vue";
import YgoBuilderMatches from "./YgoBuilderMatches.vue";
import { computed, defineComponent, ref } from "@vue/composition-api";
import { merge } from "lodash";
const cardDatabase = applicationContainer.get(APPLICATION_TYPES.CardDatabase);
const sortingService = applicationContainer.get(APPLICATION_TYPES.SortingService);
const filterService = applicationContainer.get(APPLICATION_TYPES.FilterService);
const cardService = applicationContainer.get(APPLICATION_TYPES.CardService);
export default defineComponent({
    props: {},
    components: {
        YgoFilter,
        YgoSortingOptions,
        YgoBuilderMatches,
    },
    setup(props, context) {
        const CARD_DISPLAY_LIMIT = 100;
        const reactiveFilter = ref({
            name: null,
            typeGroup: null,
            type: null,
            subType: null,
            attribute: null,
            level: null,
            linkMarker: null,
            archetype: null,
            format: null,
            banState: null,
            sets: [],
        });
        const reactiveSortingOptions = ref({
            strategy: SortingStrategy.NAME,
            order: SortingOrder.DESC,
        });
        const format = computed(() => context.root.$store.state.format.active);
        const formatCards = computed(() => filterService.filter(cardService.getUniqueByName(cardDatabase.getCards()), { format: format.value }));
        const filteredCards = computed(() => {
            const filtered = filterService.filter(formatCards.value, merge(reactiveFilter.value, {
                format: format.value,
            }));
            const sorted = sortingService.sort(filtered, reactiveSortingOptions.value);
            return sorted.slice(0, CARD_DISPLAY_LIMIT);
        });
        return {
            reactiveFilter,
            reactiveSortingOptions,
            formatCards,
            filteredCards,
        };
    },
});
