import { SortingOrder, SortingStrategy, } from "yugioh-deck-tool-core/src/main";
import { cloneDeep } from "lodash";
import { defineComponent, reactive } from "@vue/composition-api";
import VSelect from "vue-select";
export default defineComponent({
    props: {
        sortingOptions: {
            required: true,
            type: Object,
        },
    },
    components: {
        VSelect,
    },
    model: {
        prop: "sortingOptions",
        event: "change",
    },
    setup(props, context) {
        const reactiveSortingOptions = reactive(cloneDeep(props.sortingOptions));
        const sortingStrategies = [
            SortingStrategy.LEVEL,
            SortingStrategy.ATK,
            SortingStrategy.DEF,
            SortingStrategy.NAME,
            SortingStrategy.RELEASE_DATE,
        ];
        const sortingOrders = [SortingOrder.DESC, SortingOrder.ASC];
        const optionsChanged = () => context.emit("change", reactiveSortingOptions);
        return {
            sortingStrategies,
            sortingOrders,
            reactiveSortingOptions,
            optionsChanged,
        };
    },
});
